@tailwind base;
@tailwind components;
@tailwind utilities;


:root{
    --base-color: #2F201F;
    --dark-base: #261B1B;
    --light-pink: #CD9C98;
    --shadow-pink: #4C3938;
    --dark-pink: #F2887E;
    --white-grey: #787878;
    --chip-bg: #BF4B6A;
    --fancy-text: "Neuton", serif;
    --base-text: "Comfortaa", sans-serif;
}

body{
    background-color: var(--base-color);
    font-family: var(--base-text);
    font-weight: 400;
    scroll-behavior: smooth;
}

@layer utilities {
    .no-scrollbar::-webkit-scrollbar {
        display: none;
    }
    .no-scrollbar {
        -ms-overflow-style: none;
        scrollbar-width: none;
    }
}